*{box-sizing:border-box}body{font-family:-apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;background:#000;transition:0.4s ease-in-out}html,body,#root{width:100%;height:100%;margin:0;padding:0;overflow:hidden}.scrollArea{position:absolute;top:0;left:0;width:100vw;height:100vh;overflow:auto}.container{margin:0 auto;width:100%;max-width:100%;height:100%;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center}header{position:absolute;z-index:99;width:100%;padding:60px}header .header-inner{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:space-between;justify-content:space-between}header .header-inner .logo{font-weight:900;letter-spacing:2px;color:#fff}header .header-inner nav ul{margin:0;padding:0;display:-webkit-flex;display:flex}header .header-inner nav ul li{list-style:none;margin:0 60px}header .header-inner nav ul li.btn a{color:#fff;font-weight:600;background:#23232a;padding:16px 24px;border-radius:10px}header .header-inner nav ul li a{text-transform:capitalize;text-decoration:none;color:#fff}.title{font-size:10rem;text-align:center;width:100%;margin:0 auto;color:#fff}.title span{display:block}@media (max-width: 1440px){.title{font-size:6rem}}.loading{position:absolute;top:0px;left:0px;width:100%;height:100%;background:#171717;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;z-index:1000}.loading-bar-container{width:100px;height:32px;background:#272727}.loading-bar{height:32px;background:#f15946}

